import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import { connect } from 'react-redux';
import * as actions from '../../actions/actions';

const StyledByline = styled.div`
  display: flex;
  align-items: center;
  padding: 0.5rem 0;
  color: #ccc;

  a {
    color: #a0c9d7;
  }
`;

const Avatar = styled.img`
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 1rem;
  margin-right: 0.5rem;
`;

class Byline extends React.Component {
  componentDidMount() {
    console.log('about to fetchUserByUsername')
    this.props.fetchUserByUsername(this.props.username);
  }

  componentDidUpdate(prevProps) {
    console.log('component did update')
    if (prevProps.username !== this.props.username) {
      this.props.fetchUserByUsername(this.props.username);
    }
  }

  render () {
    console.log('Byline > props', this.props);
    console.log('Byline > User', this.props.user);
    if (!this.props.user) return null;

    return (
    <StyledByline>
      <Avatar src={this.props.user.avatar_url} alt={this.props.user.name} />
      <Link to={`/users/${this.props.user.username}`}>{this.props.user.name}</Link>
      &nbsp;
      {this.props.topic && <Link to={`/topics/${this.props.topic}`}>in {this.props.topic}</Link>}
      {this.props.time && `at ${this.props.time}`}
    </StyledByline>
    );
  }
}

function mapDispatchToProps(dispatch) {
  return {
    fetchUserByUsername: (username) => {
      console.log('map dispatch to props', username);
      dispatch(actions.fetchUserByUsername(username));
    }
  };
}

function MapStateToProps(state) {
  return {
    user: state.user,
    loading: state.loading
  };
}

Byline.propTypes = {
  fetchUserByUsername: PropTypes.func.isRequired,
  user: PropTypes.object.isRequired,
  username: PropTypes.string.isRequired,
  loading: PropTypes.bool.isRequired
};

export default connect(MapStateToProps, mapDispatchToProps)(Byline);
